.App {
    text-align: center;
}

body {
    font-family: "Noir Pro", sans-serif;
}

h3 {
    font-size: 14px;
}

h4 {
    font-size: 14px;
    font-weight: 300;
}

h5 {
    font-size: 14px;
    font-weight: 400;
}

p {
    font-size: 12px;
    font-weight: 100;
}

.logo {
    margin-left: 30px;
    height: 50px;
    width: 100px;
}

.menu {
    margin-top: 10px;
    margin-right: 50px;
    font-size: 16px;
}

.menulist {
    color: gray;
    list-style: none;
    display: flex;
}

.headerApp {
    width: 100%;
    margin-top: 30px;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
}

button {
    font-family: "Noir Pro", sans-serif;
    font-size: 14px;
    color: white;
    border-radius: 20px;
    border-style: none;
    padding: 5px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: #87A1FF;
    margin-left: 10px;
    transition-duration: 0.4s;
}

button:hover {
    background-color: gray;
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
}